import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
    import news from '@/views/modules/news/list'
    import shenqingxinxi from '@/views/modules/shenqingxinxi/list'
    import yonghu from '@/views/modules/yonghu/list'
    import discusstiwenxinxi from '@/views/modules/discusstiwenxinxi/list'
import discusstiwenxinxi2 from '@/views/modules/discusstiwenxinxi2/list'
    import tiwenxinxi from '@/views/modules/tiwenxinxi/list'
    import jubaoxinxi from '@/views/modules/jubaoxinxi/list'
    import zhuanyerenyuan from '@/views/modules/zhuanyerenyuan/list'
    import config from '@/views/modules/config/list'
    import newstype from '@/views/modules/newstype/list'


//2.   
export const routes = [{
    path: '/',
    name: '',
    component: Index,
    children: [{
      // main
      path: '/',
      name: '',
      component: Home,
      meta: {icon:'', title:'center', affix: true}
    }, {
      path: '/updatePassword',
      name: '',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '',
      component: center,
      meta: {icon:'', title:'center'}
    }
      ,{
	path: '/news',
        name: '',
        component: news
      }
      ,{
	path: '/shenqingxinxi',
        name: '',
        component: shenqingxinxi
      }
      ,{
	path: '/yonghu',
        name: '',
        component: yonghu
      }
      ,{
	path: '/discusstiwenxinxi',
        name: '',
        component: discusstiwenxinxi
      }
      ,{
            path: '/discusstiwenxinxi2',
            name: '',
            component: discusstiwenxinxi2
        }
      ,{
	path: '/tiwenxinxi',
        name: '',
        component: tiwenxinxi
      }
      ,{
	path: '/jubaoxinxi',
        name: '',
        component: jubaoxinxi
      }
      ,{
	path: '/zhuanyerenyuan',
        name: '',
        component: zhuanyerenyuan
      }
      ,{
	path: '/config',
        name: '',
        component: config
      }
      ,{
	path: '/newstype',
        name: '',
        component: newstype
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '*',
    component: NotFound
  }
]
//3.VueRouter  
const router = new VueRouter({
  mode: 'hash',
  /*hashhistory*/
  routes //  routes: routes
})
const originalPush = VueRouter.prototype.push
//push
VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}
export default router;
